@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

*{
  font-family:'Montserrat', sans-serif !important ;
}


input{
  border-radius: 2px !important;
  border: 1px solid rgba(128, 128, 128, 0.521) !important;
  height: 40px !important;
}

input:focus , .ant-picker{
  outline: none !important;
  box-shadow: none !important;
  border: 1px solid gray !important;
}

label{
  font-size:  16px !important;
  color:black !important ;
}

.ant-form-item{
  margin: 10px 0 !important;
}

.ant-picker{
  border-radius: 2px !important;
  border: 1px solid rgba(128, 128, 128, 0.521) !important;
  height: 40px !important;
}

.ant-picker-input input{
  border: none !important;
}

.spinner-parent{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.704);
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 99999;
}

.spinner-border{
  width: 5rem;
  height: 5rem;
  color: white !important;
}

.page-title{
  font-size: 25px;
  color:rgba(0, 0, 0, 0.833);
  font-weight: bold;
}

.home-title{
  font-size:40px;
  color:rgba(0, 0, 0, 0.833);
  font-weight: bold;
  text-align: center;
}

.card-title{
  font-size: 22px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.627);
}

.normal-text{
  font-size: 1rem;
}

.card{
  box-shadow: 0 0px 20px rgba(189, 188, 188);
  border-radius: 0 !important;
}

.primary-button{
  background-color:#4B3621 !important;
  border-color: #4B3621 !important;
  height: 40px !important;
  width:100% !important ;
  color: white !important;
  font-size: 16px !important;
  width: max-content !important;
}

.full-width-button{
  width: 100% !important;
}
p{
  color: black !important;
  padding: 0px !important;
  margin: 0px !important;
}
.anchor{
  color: white !important;
  text-decoration: underline;
  font-size:20px !important;
  cursor: pointer;
}

.anchor1{
  color: black !important;
  text-decoration: underline;
  font-size:15px !important;
  cursor: pointer;
  margin-left: 8px;
}

.anchor:hover{
  text-decoration:underline !important;
}
.cursor-pointer{
  cursor: pointer;
}

.authentication{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color:#C3B091;
}

.authentication-form{
  width: 400px;
}

.authentication .card-title{
  font-size: 1.5rem;
  font-weight: bold;
  background-color: #996515;
  color: white !important;
  max-width:max-content !important;
  padding: 7px 15px;
  margin-left: -40px;
  border-bottom-left-radius: 10px;
}